.header {
	width: 100%;
	height: 70px;
	line-height: 70px;
	align-items: center;
}

.nav__wrapper {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.logo {
	display: flex;
	align-items: center;
	column-gap: 8px;
	cursor: pointer;
}

.logo img {
	width: 1.5rem;
	height: 1.5rem;
}

.logo h1 {
	font-size: 1.2rem;
	font-weight: 700;
	color: var(--primary-color);
}

.menu {
	display: flex;
	align-items: center;
	column-gap: 2.7rem;
	margin-bottom: 0;
}

.nav__item {
	color: var(--primary-color);
	font-weight: 500;
	cursor: pointer;
}
.nav__icons {
	display: flex;
	align-items: center;
	column-gap: 1.2rem;
}

.nav__icons span i {
	font-size: 1.4rem;
	color: var(--primary-color);
	cursor: pointer;
}

.cart__icon,
.fav__icon {
	position: relative;
}
.badge {
	position: absolute;
	top: 25%;
	right: -8%;
	width: 15px;
	height: 15px;
	content: "";
	background-color: var(--primary-color);
	color: #fff;
	border-radius: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 0.7rem;
	font-weight: 600;
	z-index: 10;
}
.nav__icons .profile img {
	width: 30px;
	height: 30px;
	cursor: pointer;
	border-radius: 50%;
}

.nav__icons .profile {
	position: relative;
	z-index: 9;
}

.nav__icons .profile .profile__actions {
	position: absolute;
	top: 98%;
	left: 50%;
	width: 150px;
	z-index: 10;
	padding: 15px;
	display: flex;
	align-items: center;
	flex-direction: column;
	background: var(--card-bg-01);
	line-height: 30px;
	display: none;
	cursor: pointer;
	transform: translateX(-50%);
}

.show__profileActions {
	display: block !important;
}
.profile__logo {
	padding-bottom: 15px;
}

.mobile__menu {
	font-size: 1.3rem;
	color: var(--primary-color);
	display: none;
}

.nav__active {
	font-weight: 700 !important;
}
.sticky__header {
	width: 100%;
	height: 70px;
	line-height: 70px;
	position: sticky;
	top: 0;
	left: 0;
	z-index: 999;
	box-shadow: 3px 3px 8px -3px #ddd;
	background: #fff;
}
.center__div {
	padding-bottom: 10px;
	padding-left: 20px;
}

@media only screen and (max-width: 768px) {
	.nav__icons .profile .profile__actions {
		left: 50%; /* Start at half the width of the parent element */
		transform: translateX(-50%); /* Center it horizontally */
	}
	.navigation {
		position: fixed;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background: rgba(0, 0, 0, 0.53);
		z-index: 9999;
		display: none;
	}
	.menu {
		position: absolute;
		top: 0;
		right: 0;
		width: 250px;
		height: 100%;
		background: #fff;
		z-index: 99999;
		flex-direction: column;
		align-items: center;
		justify-content: center;
	}

	.active__menu {
		display: block;
	}

	.mobile__menu {
		display: block;
	}
	.mobile__menu span i {
		font-size: 1.1rem !important;
	}

	.logo img {
		width: 1rem;
		height: 1rem;
	}
	.logo h1 {
		font-size: 1rem;
	}

	.fav__icon i,
	.cart__icon i {
		font-size: 1.2rem !important;
	}
	.nav__icons span img {
		width: 1.4rem;
		height: 1.4rem;
	}
	.header {
		width: 100%;
		height: 60px;
		line-height: 60px;
	}
}
