.billing__form .form__group input {
	border: 1px solid var(--card-bg-02);
}

.checkout__cart {
	padding: 20px;
	background: var(--primary-color);
	color: #fff;
	border-radius: 20px;
}

.checkout__cart h6,
h4 {
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.checkout__cart h6 {
	margin-bottom: 20px;
}

.checkout__cart h4 {
	border-top: 1px solid grey;
	padding-top: 20px;
}
.byy__btn {
	border: none;
	outline: none;
	padding: 8px 20px;
	border-radius: 5px;
	background: #fff;
	color: var(--primary-color);
	cursor: pointer;
	font-size: 1rem;
	margin-top: 40px;
	font-weight: bold;
}
